import React from 'react';
import { 
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  } from 'react-router-dom'

import { 
  PreviewPage, 
  ResultPage, 
  ChoosePage, 
  TestPage, 
  LoginPage, 
  MessengerPage,

} from '@@/pages'

import './styles/index.scss';

const App = () => {

  return (
    <div className='pageWrapper'>
      <Router>
        <Switch>
          <Route exact path="/">
            <PreviewPage />
          </Route>
          <Route exact path="/result">
            <ResultPage />
          </Route>
          <Route path="/main">
            <ChoosePage />
          </Route>
          <Route exact path="/dude/*">
            <div>{"hey dude"}</div>
          </Route>
          <Route exact path="/test">
            <TestPage />
          </Route>
          <Route exact path='/login'>
            <LoginPage />
          </Route>
          <Route exact path='/messenger'>
            <MessengerPage />
          </Route>
        </Switch>
      </Router>
    </div>
  )
}

export default App;