import { io } from 'socket.io-client';
import { createStore, applyMiddleware, compose } from 'redux'

import { createLogger } from 'redux-logger'
import thunk from 'redux-thunk'
import reducer from './reducers'

import { setSearchPeople, pickDialog, setMessage, setDialog } from '@/actions'

const middleware = [ thunk ];
if (process.env.NODE_ENV !== 'production') {
  middleware.push(createLogger());
}

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(
  reducer,
  composeEnhancers(applyMiddleware(...middleware))
)
// 'http://localhost:3000'
const socket = io(window.location.origin, {transports: ['websocket', 'p']}); 

socket
  .on('connect', (ev) => {
    socket.emit('MSG', {data: 'pass', token: "123"})
  })
  .on('CLIENT_MSG', (pass) => {
    console.log('%c%s', 'font-size: 22px; color: red;', pass)
  })
  .on('RES_PRIVATE_MSG', (pass) => {
    console.log('%c%s', 'font-size: 22px; color: navy;', 'PRIVATE_MSG');
    console.log('Message:', pass);

    
    // cb()
  })
  .on('RES_MSGS', data =>  {
    console.log('%c%s', 'font-size: 22px; color: aqua;', 'GET_MSGS');
    console.log('Data:', data)
  })
  .on('RES_PEOPLE', data => {
    store.dispatch(setSearchPeople(data))
  })
  .on('RES_NEW_DIALOG', ({dialog, target}) => {
    if(target === 'me') {
      store.dispatch(pickDialog(dialog['_id'], dialog))
    } else {
      store.dispatch(setDialog(dialog))
    }
    
  })
  .on('RES_MESSAGE', ({message, dialogId}) => {
    store.dispatch(setMessage(message, dialogId))
  })
  
  .on('disconnect', (reason) => {
    console.log('%c%s', 'font-size: 22px; color: crimson;', 'DISCON:')
    console.log('Reason:',reason);
  })


let lastV = '';

store.subscribe(() => {
  const freshState = store.getState();
  const {
    lastMessage: {addres, message}, 
    room,  
    v,
    newDialogTargetId,
    messageData,
  } = freshState.dialogs;

  const token = localStorage.token;

  // какого черта это было... Почему он проходил?
  if(lastV !== v) {
    
    let checkSimbol = v.split('$$')[0];
    
    switch(checkSimbol) {
      case 'r': 
        (() => {
          
          socket.emit('JOIN_ROOM', { token, room }, (data) => {
            console.log('%c%s', 'font-size: 22px; color: green;', 'SUCCESS_JOIN');
            console.log('Rooms:', data);
          })

        })();
        break;
      case 'm': 
        (() => {
         
          socket.emit('PRIVATE_MSG', {
            token,
            payload: { addres, message }
          }, (lastRooms) => {
            console.log('%c%s', 'font-size: 22px; color: green;', 'SUCCESS_SEND_MSG');
            console.log('%c%s', 'font-size: 22px; color: aqua;', 'LAST_ROOMS:', lastRooms)
          })
          
        })();
        break;
      case 'g':
        (() => {
          console.log('SUB')
          socket.emit('GET_MSGS', {
            token
          });
        })()
        break;
      case 'init':
        (() => {
          socket.emit('JOIN', { token }, (rooms) => {
            console.log('%c%s', 'font-size: 22px; color: green;', 'SUCCESS_JOIN', rooms);
          })
        })();
        break
      case 'searchPeople':
        (() => {
          socket.emit('GET_PEOPLE', {
            token
          })
        })()
        break
      // case 'joinRoom':
      //   debugger
      //   (() => {
      //     socket.emit('JOIN_ROOM', {
      //       token, room: newDialogTargetId,
      //     })
      //   })()
      //   break
      case 'createDialog': 
        (() => {
          socket.emit('CREATE_NEW_DIALOG', {
            token,
            newDialogTargetId
          })
          
        })()
        break
      case 'createMessage': 
        (() => {
          socket.emit('CREATE_MESSAGE', {...messageData, token})
        })()
        break
      default:
        break
    }
  }

})
  

export default store