import * as types from '../constants/ActionTypes'

export const microEvent = () => ({
  type: types.MICRO_EVENT,
  payload: Math.random()
})

export const addElement = (obj) => ({
  type: types.ADD_ELEMENT,
  payload: obj
})

export const privateMsg = (payload) => ({
  type: types.PRIVATE_MSG, 
  payload
})

export const joinToRoom = (payload) => ({
  type: types.JOIN_TO_ROOM,
  payload
})

export const getMsgs = () => ({
  type: types.GET_MSGS
})

export const pickDialog = (_id, dialog = null) => ({
  type: types.PICK_DIALOG,
  payload: { _id, dialog }
})

export const setDialogTabMod = (mod) => ({
  type: types.SET_DIALOG_TAB_MOD,
  payload: { mod }
})

export const setSearchPeople = (data) => ({
  type: types.SET_SEARCH_PEOPLE,
  payload: { data }
})

export const createNewDialog = (id = null) => ({
  type: types.CREATE_NEW_DIALOG,
  payload: { userId: id }
})

export const createMessage = (text) => ({
  type: types.CREATE_MESSAGE,
  payload: { text }
})

export const setMessage = (message, dialogId) => ({
  type: types.SET_MESSAGE,
  payload: { message, dialogId }
})

export const setDialog = (dialog) => ({
  type: types.SET_DIALOG,
  payload: { dialog }
})

export const joinRoom = userId => ({
  type: types.JOIN_ROOM,
  payload: { userId }
})

export {createUser, loginUser, checkAuth} from './network'