import React, { useState } from 'react'
import { connect } from 'react-redux'
import classNames from 'classnames'

import { Input, Button } from 'antd'
import { Message, DateElement } from '@@/components'

import { createMessage } from '@/actions'

import './Chat.scss'

const { TextArea } = Input

export const Chat = (
  {
  choosenDialogId,
  readyMessengesData,
  dialogs, 
  newDialogTargetId,

  createMessage,
}) => {
  
  const [inputValue, setInputValue] = useState('')

  const dialogIsPicked = choosenDialogId !== null

  function submitHandler () {
    if (dialogIsPicked) {
      createMessage(inputValue);
      setInputValue('');
    }
  }

  const messages = (() => {
    const res = [];
    readyMessengesData
    .forEach(({date, messages}) => {
      res.push( <DateElement date={date} />);
      messages
      .forEach(data => {
        res.push( <Message {...data} />)
      })
    });
    return res;
  })()

  // function createDialogHandler () {
  //   if(!dialogs.some(({dialog: {author, partner}}) => {
  //     return ((newDialogTargetId === author['_id']) || (newDialogTargetId === partner['_id']))
  //   })) {
  //     createNewDialog()
  //   }
  // }
  
  return (
    <section className='chat'>
      <div className='chat__main'>
        <div className={classNames(
          'chat__main_wrapper', 
          !dialogIsPicked ? 'chat__main_wrapper-help' : null
          )}>
          { dialogIsPicked
           ? messages // [...messages, ...messages, ...messages]
           : 'Pick dialog'
          }
        </div>
      </div>
      <div className='chat__inputArea'>
        <div className='chat__inputArea_textArea'>
          <TextArea autoSize 
            // onFocus={createDialogHandler}
            onChange={ev => setInputValue(ev.target.value)}
            value={inputValue} 
          />
        </div>
        <div className='chat__inputArea_button'>
          <Button onClick={submitHandler}>Send</Button>
        </div>
      </div>
      
    </section>
  )
}

const mapStateToProps = (
  {dialogs: 
    {
      choosenDialogId, 
      readyMessengesData, 
      dialogs, 
      newDialogTargetId,
    }}) => ({
  choosenDialogId, 
  readyMessengesData,
  dialogs, 
  newDialogTargetId,
})

const mapDispatchToProps = {
  createMessage
}

export default connect(mapStateToProps, mapDispatchToProps)(Chat)
